.container {
  padding: 32px;
  width: 400px;
  outline: 2px solid red;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
}

.services__input {
  position: relative;
  width: 100%;
  height: 30px;
  padding-left: 5px;
  border: 1px solid black;
  border-radius: 5px;
  outline: none;
  margin-top: 10px;

  &:focus {
    border: 2px solid black;
  }
}

.services__label {
  position: relative;
  display: block;
  &::after {
    display: block;
    position: absolute;
    right: 0;
    top: 29px;
    content: '';
    width: 30px;
    height: 29px;
    border-left: 0.5px solid black;
    z-index: 2;
  }
}

.services__label__arrowIcon {
  position: absolute;
  top: 33px;
  right: 3px;
  width: 24px;
  height: 20px;
  z-index: 3;
  transition: transform 300ms linear;

  .services__input:focus + & {
    transform: rotate(180deg);
  }
}
